<template>
  <div class="hello">
    <div v-on:click="Tone.start()">Click here to activate audio.</div>
    <h1>Music</h1>
    <div class="play" v-on:click="play()">PLAY</div>
  </div>
</template>

<script>
import Tone from "tone";
export default {
  name: "Music",
  props: {
    msg: String
  },
  methods:{
    play:function(){
      let chords = [
        ["C4", "E4", "G4","B4"],
        ["C4", "Eb4", "G4","B4"],
        ["C4", "Eb4", "G4","Bb4"],
        ["C4", "E4", "G#4","B4"],
      ]
      let index = Math.floor(chords.length * Math.random())
      this.synth.triggerAttackRelease(chords[index], "8n");
    }
  },
  mounted(){
    this.Tone = Tone;
    let autoWah = new Tone.AutoWah().toMaster();
    autoWah.Q.value = 6;
    autoWah.sensitivity = -20;
    let chorus = new Tone.Chorus().connect(autoWah);
    //a polysynth composed of 6 Voices of Synth
    let synth = new Tone.PolySynth(6, Tone.FMSynth, {
      oscillator : {
        type : "square"
      }
    }).connect(chorus);
    //set the attributes using the set interface
    synth.set("detune", -1200);
    //play a chord
    this.synth = synth;

  }
};
</script>

